'use client';

import { useState } from 'react';
import { runNewProjectAgent, getNewProjectAgentPrompt, NewProjectUserInput } from '@/services/new_project_agent';

const initialInput: NewProjectUserInput = {
  brand_name: 'SuperCasino',
  target_country: 'United States',
  target_language: 'English',
  site_type: 'multi-page',
};

export default function NewProjectAgentTestPage() {
  const [userInput, setUserInput] = useState<NewProjectUserInput>(initialInput);
  const [prompt, setPrompt] = useState<string>('');
  const [reply, setReply] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setUserInput(prev => ({ ...prev, [name]: value }));
  };

  const handleRunAgent = async () => {
    setLoading(true);
    setError('');
    setReply('');
    const filledPrompt = getNewProjectAgentPrompt(userInput);
    setPrompt(filledPrompt);
    try {
      const result = await runNewProjectAgent(userInput);
      setReply(result);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Unknown error');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4">
      <div className="max-w-2xl mx-auto">
        <h1 className="text-3xl font-bold mb-8">New Project Agent Test</h1>
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-xl font-semibold mb-4">User Input</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
            <div>
              <label className="block text-sm font-medium mb-1">Brand Name</label>
              <input
                type="text"
                name="brand_name"
                value={userInput.brand_name}
                onChange={handleChange}
                className="w-full border px-3 py-2 rounded"
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">Target Country</label>
              <input
                type="text"
                name="target_country"
                value={userInput.target_country}
                onChange={handleChange}
                className="w-full border px-3 py-2 rounded"
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">Target Language</label>
              <input
                type="text"
                name="target_language"
                value={userInput.target_language}
                onChange={handleChange}
                className="w-full border px-3 py-2 rounded"
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">Site Type</label>
              <select
                name="site_type"
                value={userInput.site_type}
                onChange={handleChange}
                className="w-full border px-3 py-2 rounded"
              >
                <option value="single-page">Single Page</option>
                <option value="multi-page">Multi Page</option>
              </select>
            </div>
          </div>
          <button
            onClick={handleRunAgent}
            disabled={loading}
            className="bg-blue-600 text-white px-6 py-2 rounded font-semibold hover:bg-blue-700 disabled:bg-blue-400 disabled:cursor-not-allowed"
          >
            {loading ? 'Running...' : 'Run New Project Agent'}
          </button>
        </div>

        {prompt && (
          <div className="bg-gray-100 rounded-lg p-4 mb-6">
            <h3 className="font-semibold mb-2">Filled Prompt</h3>
            <pre className="whitespace-pre-wrap text-sm text-gray-800">{prompt}</pre>
          </div>
        )}

        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <h3 className="text-red-800 font-semibold mb-2">Error</h3>
            <p className="text-red-700">{error}</p>
          </div>
        )}

        {reply && (
          <div className="bg-green-50 border border-green-200 rounded-lg p-4">
            <h3 className="text-green-800 font-semibold mb-2">Claude Reply</h3>
            <pre className="whitespace-pre-wrap text-sm text-gray-800">{reply}</pre>
          </div>
        )}
      </div>
    </div>
  );
} 