import { callClaudeAPI } from '@/lib/claude-api';
import { NEW_PROJECT_AGENT_PROMPT } from './system-prompts';

export interface NewProjectUserInput {
  brand_name: string;
  target_country: string;
  target_language: string;
  site_type: string;
  site_type_selected_at?: string;
  structure?: any;
}

function fillPrompt(template: string, userInput: NewProjectUserInput): string {
  return template
    .replace('{project_name}', userInput.brand_name)
    .replace('{user_requirements}', `Country: ${userInput.target_country}, Language: ${userInput.target_language}`)
    .replace('{site_type}', userInput.site_type)
    .replace('{industry}', 'Casino');
}

export async function runNewProjectAgent(userInput: NewProjectUserInput): Promise<string> {
  const prompt = fillPrompt(NEW_PROJECT_AGENT_PROMPT, userInput);
  const reply = await callClaudeAPI(prompt);
  return reply;
}

// For debugging: get the filled prompt
export function getNewProjectAgentPrompt(userInput: NewProjectUserInput): string {
  return fillPrompt(NEW_PROJECT_AGENT_PROMPT, userInput);
} 